<template>
  <div class="output">
    <div class="input">
      <label class="host" for="command">{{ label }}</label>
      <label class="separator">{{ separator }}</label>
      <input type="text" name="command" id="command" ref="command" autocomplete="off" spellcheck="false" :value="command" disabled>
    </div>
    <div class="message">
      <About v-if="command === 'about'" />
      <Education v-if="command === 'education'" />
      <Experience v-if="command === 'experience'" />
      <Help v-if="command === 'help'" />
      <Skills v-if="command === 'skills'" />
      <p v-if="!this.commands.includes(command)">
        sh: Unknown command: {{ command }}
        <br>
        See `help` for info
      </p>
    </div>
  </div>
</template>

<script>
import terminal from "@/config/terminal";
import About from "@/components/commands/About";
import Education from "@/components/commands/Education";
import Experience from "@/components/commands/Experience";
import Help from "@/components/commands/Help";
import Skills from "@/components/commands/Skills";

export default {
  name: "Output",
  components: {
    About,
    Education,
    Experience,
    Help,
    Skills
  },
  data() {
    return {
      label: terminal.LABEL,
      separator: terminal.SEPARATOR,
      commands: ['about', 'education', 'experience', 'help', 'skills']
    }
  },
  props: {
    command: String
  },
  created() {
    if (this.command === "clear") {
      this.$emit('clear');
    }
  }
}
</script>

<style scoped>
.output{
  word-break: break-word;
}

.input {
  display: flex;
}

.host, .separator, .input input {
  font-size: 15px;
}

.host {
  color: #15d4b4;
}

.separator {
  color: chocolate;
  margin: 0 5px 0 10px;
}

.input input {
  background-color: transparent;
  border: none;
  outline: none;
  color: white;
  font-family: 'Fira Code', Helvetica, Arial, sans-serif;
  box-sizing: border-box;
  flex-grow: 1;
  margin-left: 3px;
  text-decoration: none;
}

.message {
  padding: 0 0 15px 0;
}

.message p {
  color: white;
  font-size: 15px;
  margin: 0;
}
</style>