<template>
  <div class="skill">
    <div class="name">
      <p>
        <em :class="icon"></em> {{ name }}
      </p>
    </div>
    <div class="rating">
      <em class="fa-solid fa-star star-colored" v-for="n in parseInt(value)" :key="n"></em>
      <em class="fa-solid fa-star" v-for="n in parseInt(max - value)" :key="n"></em>
    </div>
  </div>
</template>

<script>
export default {
  name: "Skill",
  props: {
    icon: String,
    name: String,
    value: String,
    max: String
  }
}
</script>

<style scoped>
p {
  margin: 0;
}

.skill {
  display: flex;
  width: fit-content;
  padding: 10px;
  border: solid 2px #15d4b4;
  border-radius: 10px;
  margin: 10px;
}

.rating {
  padding-left: 10px;
}

.rating em {
  padding-left: 2px;
}

.star-colored {
  color: #FDCC0D;
}
</style>