<template>
  <div class="input">
    <label class="host" for="command">{{ label }}</label>
    <label class="separator">{{ separator }}</label>
    <input type="text" name="command" id="command" ref="command" autocomplete="off" spellcheck="false" v-model="command" @keydown.enter="sendCommand">
  </div>
</template>

<script>
import terminal from "@/config/terminal";

export default {
  name: "Input",
  data () {
    return {
      label: terminal.LABEL,
      separator: terminal.SEPARATOR,
      command: ""
    }
  },
  methods: {
    sendCommand() {
      if (this.command.trim() === "") {
        return;
      }

      this.$emit('commands', this.command);
      this.command = "";
    },
    triggerCommandFocus() {
      this.$refs.command.focus();
    }
  },
  mounted() {
    this.triggerCommandFocus();
  }
}
</script>

<style scoped>
  .input {
    display: flex;
  }

  .host, .separator, .input input {
    font-size: 15px;
  }

  .host {
    color: #15d4b4;
  }

  .separator {
    color: chocolate;
    margin: 0 5px 0 10px;
  }

  .input input {
    background-color: transparent;
    border: none;
    outline: none;
    color: white;
    font-family: 'Fira Code', Helvetica, Arial, sans-serif;
    box-sizing: border-box;
    flex-grow: 1;
    margin-left: 3px;
    text-decoration: none;
  }
</style>