<template>
  <div class="terminal">
    <div class="toolbar">
      <div class="icons">
        <div  class="icon close"></div>
        <span class="icon minimize"></span>
        <span class="icon maximize"></span>
      </div>
      <div class="title">
        <p>{{ label }}: ~</p>
      </div>
    </div>
    <div class="body">
      <Output v-for="command in this.commands" :command="command" :key="command" @clear="clearTerminal"/>
      <Input @commands="setCommands"/>
    </div>
  </div>
</template>

<script>
import Input from "@/components/Input";
import Output from "@/components/Output";
import terminal from "@/config/terminal"

export default {
  name: "Terminal",
  components: {
    Input,
    Output
  },
  data() {
    return {
      commands: [],
      label: terminal.LABEL,
    }
  },
  methods: {
    setCommands(o) {
      this.commands.push(o);
    },
    clearTerminal() {
      this.commands = []
    }
  }
}
</script>

<style scoped>
  .terminal {
    background-color: rgb(32,32,32);
    width: 900px;
    height: 500px;
    border-radius: 10px;
    box-shadow: rgba(0, 0, 0, 0.19) 0px 10px 20px, rgba(0, 0, 0, 0.23) 0px 6px 6px;
  }

  .toolbar {
    width: 100%;
    padding-left: 10px;
    box-sizing: border-box;
    display: flex;
  }

  .icon {
    width: 16px;
    height: 16px;
    display: inline-block;
    border-radius: 50%;
    margin: 15px 4px 10px 4px;
    box-sizing: border-box;
  }

  .close {
    background-color: red;
  }

  .maximize {
    background-color: orange;
  }

  .minimize {
    background-color: mediumseagreen;
  }

  .title {
    display: flex;
    align-items: center;
    justify-content: center;
    color: white;
    font-size:  12px;
    margin-left: 310px;
  }

  .body {
    padding: 15px 20px 15px 20px;
    width: 100%;
    box-sizing: border-box;
    display: flex;
    flex-direction: column;
    overflow: auto;
    height: calc(100% - 46px);
  }
</style>